
$cols: 8;

@mixin W($prop, $n, $plus: 0px) {
  #{$prop}: calc(100vw * #{$n} / #{$cols} + #{$plus});
}

@mixin H($prop, $n, $plus: 0px) {
  #{$prop}: calc(100vh * #{$n} / #{$cols} + #{$plus});
}

@mixin grid($start, $width, $rtl: false) {
  $dir: if(not $rtl, left, right);
  $nodir: if($rtl, left, right);
  position: absolute;
  @include W($dir, $start);
  @include W(width, $width);
  #{$nodir}: inherit;
}

$linear: cubic-bezier(0.25, 0.25, 0.75, 0.75) !default;
$ease: cubic-bezier(0.25, 0.1, 0.25, 1) !default;
$ease-in: cubic-bezier(0.42, 0, 1, 1) !default;
$ease-out: cubic-bezier(0, 0, 0.58, 1) !default;
$ease-in-out: cubic-bezier(0.42, 0, 0.58, 1) !default;

$easeInQuad: cubic-bezier(0.55, 0.085, 0.68, 0.53) !default;
$easeInCubic: cubic-bezier(0.55, 0.055, 0.675, 0.19) !default;
$easeInQuart: cubic-bezier(0.895, 0.03, 0.685, 0.22) !default;
$easeInQuint: cubic-bezier(0.755, 0.05, 0.855, 0.06) !default;
$easeInSine: cubic-bezier(0.47, 0, 0.745, 0.715) !default;
$easeInExpo: cubic-bezier(0.95, 0.05, 0.795, 0.035) !default;
$easeInCirc: cubic-bezier(0.6, 0.04, 0.98, 0.335) !default;
$easeInBack: cubic-bezier(0.6, -0.28, 0.735, 0.045) !default;

$easeOutQuad: cubic-bezier(0.25, 0.46, 0.45, 0.94) !default;
$easeOutCubic: cubic-bezier(0.215, 0.61, 0.355, 1) !default;
